<template>
  <div style="height:100%;position:relative">
    <div class="mint-searchbar">
      <div class="mint-searchbar-inner">
        <i class="mintui mintui-search"></i>
        <input @click="clickSearch" placeholder="搜索" class="mint-searchbar-core">
      </div>
    </div>
    <div class="loadmore-wrapper" ref="wrapper">
      <mt-loadmore :autoFill="false" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
          <mt-swipe :show-indicators="false" id="advs" :auto="6000">
            <mt-swipe-item v-for="item in advs" :key="item.image">
              <img class="advs-img" :src="item.image"/>
            </mt-swipe-item>
          </mt-swipe>
          <div class="flex">
            <span><img src="../assets/img/explorer_hot.png"/>本周最热</span>
            <span><img src="../assets/img/explorer_collection_set.png"/>收藏集</span>
            <span><img src="../assets/img/explorer_offline.png"/>线下活动</span>
            <span><img src="../assets/img/explore_post.png"/>专栏</span>
          </div>
          <mt-cell title="热门文章">
            <img class="settings" src="../assets/img/settings.png"/>定制热门
            <img class="explorer-hot-small" slot="icon" src="../assets/img/explorer_hot_small.png">
          </mt-cell>
          <mt-cell class="readings" v-for="item in readings" :key="item.id" 
          :title="item.title" :label="getDesc(item.likes,item.author)"></mt-cell>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { ADVS, READINGS } from "../api/paths";
let { advs } = ADVS;
let { readings } = READINGS;

export default {
  name: "explore",
  data() {
    return {
      allLoaded: false,
      advs: [],
      readings: [],
      scrollTop: 0
    };
  },
  computed: {
    ...mapState(["tab"])
  },
  activated() {
    if (this.tab !== "explore") {
      this.$store.commit("changeTab", "explore");
    }
    if (this.scrollTop) {
      this.$refs.wrapper.scrollTop = this.scrollTop;
    }
  },
  created() {
    this.$http.get(advs).then(res => {
      if (res.data.errcode === 0) {
        this.advs = res.data.data;
      }
    });
    this.$http.get(readings).then(res => {
      if (res.data.errcode === 0) {
        this.readings = res.data.data;
      }
    });
  },
  mounted() {
    var that = this;
    this.$refs.wrapper.onscroll = function() {
      that.scrollTop = this.scrollTop;
    };
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  methods: {
    clickSearch() {
      this.$router.push("/search");
    },
    loadTop() {
      this.$http.get(readings).then(res => {
        if (res.data.errcode === 0) {
          this.readings = res.data.data;
          this.$refs.loadmore.onTopLoaded();
        }
      });
    },
    loadBottom() {
      this.$http.get(readings).then(res => {
        if (res.data.errcode === 0) {
          this.readings = this.readings.concat(res.data.data);
          this.$refs.loadmore.onBottomLoaded();
        }
      });
    },
    getDesc(likes, author, date) {
      return likes + "人喜欢·" + author;
    }
  }
};
</script>
<style>
.readings .mint-cell-wrapper {
  height: 70px;
}
</style>
<style scoped>
#advs {
  height: 120px;
}
#advs .advs-img {
  width: 100%;
  height: 100%;
}
.flex {
  display: flex;
  justify-content: space-around;
  background: white;
  margin-top: 10px;
  font-size: 13px;
  text-align: center;
  height: 90px;
  align-items: center;
  margin-bottom: 10px;
}
.flex span img {
  width: 50px;
  height: 50px;
  display: block;
  margin-bottom: 5px;
}
.explorer-hot-small,
.settings {
  width: 25px;
  height: 25px;
}

.loadmore-wrapper {
  overflow: scroll;
  overflow-x: hidden;
  width: 100%;
  position: absolute;
  top: 44px;
  bottom: 55px;
}
</style>




